body {
  background-color: #f0f0f0;
}

.meals {
  align-items: center;
  background: #ecf0f1;
  color: #464646;

  /* display: flex; */
  flex-direction: column;
  font-family: sans-serif;
  justify-content: center;
  width: 100vw;
}

.rocksGlass {
  animation: shake 1s infinite ease;
}

